.lang-panel {
	position: fixed;
	top: 20px;
	right: 20px;
	z-index: 50;
	font-family: MaryadPro;
	.language:before {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		// background: $black
		background: $oceanblue;
		opacity: 0.5;
		border-radius: 40px;
		@include  transition-ease(0.5s);
 }		// @include box-shadow(inset 0px 0px 350px 150px #000)

	.language {
		position: relative;
		display: inline-block;
		border-radius: 40px;
		width: 30px;
		height: 30px;
		text-align: center;
		line-height: 30px;
		color: $white;
		cursor: pointer;
		margin-left: 10px;
		background: none;
		border: none;
		span {
			opacity: 1;
			position: relative;
			z-index: 16;
			font-size: 16px;
			position: absolute;
			top: 50%;
			left: 50%;
			@include transform(translate(-50%, -50%)); } }

	.language:hover:before {
		opacity: 1; } }

.none {
	display: none; }

.flex {
	@include flex; }
.block {
	display: block; }
.va-middle {
	@include transform(translateY(-50%));
	top: 50%; }

