#first {
	background: url("/img/first-bg.png") no-repeat 50% 50%;

	background-size: cover;

	width: 100%;
	height: 100%;
	font-size: 50px;
	color: $white;
	display: table;
	// text-align: center
	// vertical-align: middle
	.logo,
	.arr-down,
	.logo img {
		position: absolute;
		top: 45%;
		left: 50%;
		@include transform(translate(-50%, -50%)); }
	.motto {
		position: absolute;
		top: 65%;
		left: 50%;
		@include transform(translate(-50%, -50%));
		width: 100%;
		text-align: center;
		font-family: MaryadPro;
		text-transform: capitalize;
		font-size: 40px;
		letter-spacing: 2px;
		span {
			color: $oceanblue;
			text-shadow: 0 0 4px $darkcrimson;
			font-weight: bold; } }
	.arr-down {
		top: 85%;
		// display: none
		i {
			animation: bounceInDownArr 1.5s infinite; } } }

.arr-bounce {
	animation: bounceInDownArr 1.5s infinite; }
.arr-fadein {
	animation: fadeIn 1s; }
.arr-fadeout {
	animation: fadeOut 1s; }

.opacity-0 {
	opacity: 0;
	@include transition-ease(1s); }

.site-h {
	text-transform: uppercase;
