	background-size: cover;
	position: relative;
	padding-bottom: 2.5%;
	.flickity-viewport {
		border-radius: 5px; }
	.flickity-slider {
		width: 100%; }
	.carousel {
		width: 75%;
		// height: 70%
		margin: 0 auto 20px;
		// padding-bottom: 30px
		// margin-bottom: 2%
		// position: absolute
		// top: 50%
		// left: 50%
		// transform: translate(-50%, -50%)
		background: none;
		button {
			width: 7%;
			height: 70%;
			// background: red
			border-radius: 0;
			padding: 0; }
		.next {
			right: 0px;
			border-radius: 5px 0 0 5px; }
		.previous {
			left: 0;
			border-radius: 0	5px 5px	0; }
		.carousel-cell {
			// background: none
			height: 100%;
			.more {
				max-width: 150px;
				width: 30%;
				min-width: 50px;

				max-height: 40px;
				height: 30%;
				min-height: 10px;

				border-radius: 5px;
				cursor: pointer;
				position: absolute;
				bottom: 10px;
				right: 10px;
				font-size: 20px;
				// @include transition(.5s)
				div {
					position: absolute;
					top: 50%;
					left: 50%;
					@include transform(translate(-50%, -50%));
					color: $white;
					opacity: 1;
					letter-spacing: 1px; } }
			.more:before {
				content: '';
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				background: $black;
				opacity: .7;
				border-radius: 5px;
				@include transition(.5s); }

			// .more:hover
			/// font-size: 24px
			/// background: $oceanblue
			/// @include transition(.5s)
			.more:hover:before {
				background: $oceanblue;
				// @include transition(.5s)
				opacity: 1; } }
		.djbr {
			background: url("/img/img-dj.png")	no-repeat 50% 0%; }
		.lash {
			background: url("/img/img-lsp.png")	no-repeat 50% 0%; }
		.lb {
			background: url("/img/img-ts.png")	no-repeat 50% 0%; }
		.pfbl {
			background: url("/img/img-prb.png")	no-repeat 50% 0%; }
		.new {
			background: url("/img/new.png")	no-repeat 50% 50%; }
		.cover {
			background-size: cover; } }


	.flickity-page-dots {
		.dot {
			background: $white; } }

	.gallery {
		background: $orange;
		margin: 3% 0 2.5%; } }

.carousel-cell:before,
.carousel-cell__number {
	display: none; }


